.navbar-brand {
    img {
        width: 90px; } }

.navbar_black {
    background-color: #292929; }

.carousel-item {
    position: relative;
    img {
        height: 500px;
        object-fit: cover; } }

.content {
    position: absolute;
    color: #d8d8d8;
    max-width: 900px;
    left: 12%;
    bottom: 15%;
    z-index: 2; }

.overlay {
    position: absolute;
    width: 100%;
    height: 100%;
    background-color: black;
    opacity: 0.4; }

.carousel-control-next {
    i {
        font-size: 72px; } }

.carousel-control-prev {
    i {
        font-size: 72px; } }
